@keyframes myMove {
    from {
        transform: scale(1.1, 1.1);
        transform-origin: 50% 50%;
    }
    to {
        transform: scale(1.2, 1.2);
        transform-origin: 50% 50%;
    }
}

@keyframes bonbon {
    0%,100% {
        bottom:40px;
    }
    50% {
        bottom:10px;
    }
}